.accordion {
	margin-left: 40px;
	margin-right: 40px;
	border-left: 1px solid #c8c7c7;
	border-right: 1px solid #c8c7c7;
	overflow: hidden;
	margin-top: 100px;
	text-rendering: optimizelegibility;
	height: calc(100vh - 285px);
	margin-bottom: 40px;

		.accordion__item {

			&:not(:last-child) {
				.name {
					border-right: 1px solid #c8c7c7;

				}
			}
		}
}


.accordion__number {
	font-weight: 300;
	font-size: 120px;
	line-height: 120px;
	justify-self: flex-start;
	transition: all 0.3s;
}

.accordion__inner {
	color: black;
}

.accordion__list {
	display: flex;
	margin: 0;
	padding: 0;
	overflow: hidden;
	list-style-type: none;
	height: 100%;
}

.accordion__item {
	flex: 1;
	display: flex;
	align-items: stretch;
	padding: 0 60px;
	cursor: pointer;
	height: 100%;
	transition: all .5s ease;
	border-right: 1px solid #c8c7c7;
	border-left: 1px solid #c8c7c7;
	//&:not(:last-child) {
	//	border-right: 1px solid #c8c7c7;
	//
	//}
	&:hover {
		flex: 1.1;
		border-color: black;
		.accordion__title {
			font-weight: 900;
			transition: all 0.3s;
		}
	}

	.accordion__title {
		color: black;
		font-size: 24px;
		font-weight: 300;
		margin: 0;
		transition: all 0.3s;

	}
	&.hide {
		padding: 0;
		flex: 0;
		width: 30px;
		position: relative;
		content: '';
		display: block;
		min-width: 30px;
		&:hover {
			min-width: 50px;
			.accordion__left {
				.accordion__title {
					left: -75px;
				}
			}
		}
		.accordion__number {
			font-size: 0;
			transition: all 0.3s;
		}

		.accordion__left {
			flex: 0;


			.accordion__title {
				font-size: 20px;
				transform: rotate(-90deg);
				white-space: nowrap;
				writing-mode: lr-tb;
				justify-content: flex-start;
				transition: all 0.3s;
				align-items: flex-start;
				height: auto;
				align-self: flex-end;
				position: absolute;
				left: -85px;
				bottom: 150px;
				display: block;
				width: 200px;
				z-index: 10;
				text-align: left;
			}
		}


	}


	&.active {
		flex: 5;
		background: #fff;
		cursor: default;
		display: flex;
		.accordion__number {
			font-size: 120px;
			transition: all 0.3s;
		}

		&:hover {
			border-color: #c8c7c7;
			.accordion__left {
				.accordion__title {
					left: auto;
				}
			}
		}

		.accordion__left {
			padding: 60px;
			border-right: 1px solid #c8c7c7;
			.accordion__title {
				font-size: 20px;
				transform: rotate(0);
				white-space: nowrap;
				writing-mode: lr-tb;
				justify-content: flex-start;
				transition: all 0.3s;
				align-items: flex-start;
				height: auto;
				align-self: flex-start;
				position: relative;
				left: auto;
				bottom: auto;
				display: inline-block;
				width: auto;
				z-index: 10;
				text-align: left;
			}
		}



		.accordion__content {
			flex: 4;
			opacity: 1;
			transform: scaleX(1);
			width: auto;
			position: relative;
			margin-left: 60px;
				p {
					width: auto;
					transition: all .25s .1s ease-out;

				}
		}
	}

	.accordion__left {
		flex: 1;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
		width: 100%;
		margin: 0;
		padding: 60px 0;
		text-align: center;
		color: black;
		transition: all 0.3s;

		h2 {
			margin: 0;
			transform: rotate(-90deg);
			white-space: nowrap;
		}
	}

	.accordion__content {
		flex: 0;
		display: flex;
		align-items: center;
		margin: 0;
		position: absolute;
		transform: scaleX(0);
		padding: 0;
		opacity: 0;
		transition: all .25s .1s ease-out;
			p {
				width: 0;
				transition: all .25s .1s ease-out;

			}
	}
}

.accordion__item--active {
	.accordion__title {
		font-weight: 900;
	}
}

.accordion__content {
	align-self: flex-start;
	content: '';
	max-width: 890px;
	width: 100%;
}


.accordion__content-title {
	font-size: 30px;
	font-weight: 300;
}
